/* 
 * Navbar 
*/
nav {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 56px;
    padding: 0.5rem;
    background-color: var(--primary);
}

.nav-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
}

/*
 * Sidenav
*/
.sidenav {
    position: fixed;
    top: 0;
    left: -100%;
    display: block;
    width: 80%;
    max-width: 300px;
    height: 100vh;
    background-color: #fff;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 
                0 3px 1px -2px rgba(0,0,0,0.12), 
                0 1px 5px 0 rgba(0,0,0,0.2);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    z-index: 999;
}

.sidenav.active {
    left: 0;
}

.sidenav li > a {
    color: rgba(0,0,0,0.87);
    display: block;
    font-size: 1rem;
    font-weight: 500;
    height: 48px;
    line-height: 48px;
    padding: 0 2rem;
}

/*
 * Cards
*/
.card {
    position: relative;
    margin: .5rem 0 1rem 0;
    border-radius: 2px;
  }

/*
 * Shadows
*/
.card,nav {
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 
                0 3px 1px -2px rgba(0,0,0,0.12), 
                0 1px 5px 0 rgba(0,0,0,0.2);
}


.hi-user {
    padding-left: 1rem;
    color: #FFF;
}


:root {
    --primary-dark: #6a0080;
    --primary-light: #d05ce3;
    --primary: #9c27b0;
    --secondary-dark: #087f23;
    --secondary-light: #80e27e;
    --secondary: #4caf50;
    --white: #FFF;
    --black: #000;
}

.row {
    margin: auto;
    margin-bottom: 1.25rem;
    
}

.col {
    padding: 0 .75rem;
    min-height: 1px;
}

.input-field {
    position: relative;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.input-field input, .input-field textarea {    
    width: 100%;
    height: 3rem;

    margin: 0 0 1.5rem 0;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #9e9e9e;
    padding: 0;

    background-color: transparent;
    outline: none;

    font-size: 1rem;
}

.input-field textarea {
    padding: 0.8rem 0;
}

.input-field label {
    position: absolute;
    top: 0;
    left: .75rem;

    font-size: 1rem;
    text-align: left;
    text-align: initial;
    color: #9e9e9e;
    cursor: text;

    -webkit-transition: color .2s ease-out, -webkit-transform .2s ease-out;

    transition: color .2s ease-out, -webkit-transform .2s ease-out;

    transition: transform .2s ease-out, color .2s ease-out;

    transition: transform .2s ease-out, color .2s ease-out, -webkit-transform .2s ease-out;

    -webkit-transform-origin: 0% 100%;

            transform-origin: 0% 100%;
    -webkit-transform: translateY(12px);
            transform: translateY(12px);
  
}

.input-field input:focus, .input-field input:active, .input-field input.non-empty,
.input-field textarea:focus, .input-field textarea:active {
    border-bottom: 1px solid #4caf50;
    border-bottom: 1px solid var(--secondary);
}

.input-field input:focus + label, .input-field input:active + label, .input-field input.non-empty + label,
.input-field textarea:focus + label, .input-field textarea:active + label {
    -webkit-transform: translateY(-14px) scale(0.8);
            transform: translateY(-14px) scale(0.8);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    color: #4caf50;
    color: var(--secondary);
  }
