/*
 * Folder Buttons (styles)
 *
 * Types:
 *      Rised Button
 *      Unelevated Button
 *      Text Button
 *      Outline Button 
 *
*/

/*
 * Reset Button
*/
button {
    background-color: var(--white);
}

/*
 * Styles of button
*/
.btn {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 2.25rem;
    line-height: 2.25rem;
    border: 0;
    border-radius: 2px;
    padding: 0 1rem;
    
    font-size: 0.875rem;
    letter-spacing: 0.5px;
    overflow: hidden;
    outline: none;
    cursor: pointer;
    text-transform: uppercase;
    color: var(--white);

    -webkit-transition: 0.3s ease-out;

    transition: 0.3s ease-out;
}

.btn[type="submit"]:disabled {
    background-color: #e0e0e0;
    box-shadow: none;
    color: #111;
}  

/*
 * Button Hover
*/
.btn-rised:hover {
    background-color: var(--secondary);
}

/*
 * Rised Button and Unelevated Button
*/
.btn-unelevated, .btn-rised {
    background-color: var(--secondary-dark);
    color: var(--white);
}
  
/*
 * Text Button and Outline Button
*/ 
.btn-text, .btn-outline, .bnt-flat {
    color: var(--primary);
}

.btn-text { 
    text-decoration: underline; 
    
}
  
.btn-outline {
    border: solid 2px var(--primary);
}
  

/*
 * Circle Button
*/
.mdl-circle, .mdl-circle-unelevated {
position: relative;
width: 70px;
height: 70px;
border: solid 2px #51629f;
border-radius: 50%;
color: #51629f;
overflow: hidden;
outline: none;
cursor: pointer;
}
  
.mdl-circle i, .mdl-circle-unelevated i{
font-size: 1.5rem;
}

.mdl-circle-unelevated {
border: 0;
background: #51629f;
color: white;
}

/*
 * Shadows
*/

.btn-rised {
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 
                0 3px 1px -2px rgba(0,0,0,0.12), 
                0 1px 5px 0 rgba(0,0,0,0.2);
}

.btn-rised:hover {
    box-shadow: 0 3px 3px 0 rgba(0,0,0,0.14), 
                0 1px 7px 0 rgba(0,0,0,0.12), 
                0 3px 1px -1px rgba(0,0,0,0.2);
}

/*
 * Icons
*/
.btn i {
    font-size: 1.3rem;
    line-height: inherit;
}
.btn i.right { 
    float: right;
    margin-left: 15px;
}
.btn i.left { 
    float: left;
    margin-right: 15px;
